import avatar from '../../../../components/yq-avatar/yq-avatar.vue';
export default {
	data() {
		return {
			form_data:{
				url:'',
				name:'',
				intro:'',
				addr:'',
			},
			save_loading:false,
		};
	},
	components: {
		avatar
	},
	methods:{
		myUpload(rsp) {
			console.log(rsp)
			this.form_data.url = rsp.path; //更新头像方式一
			//rsp.avatar.imgSrc = rsp.path; //更新头像方式二
		},
		formSubmit(e) {
			console.log(e)
			if(this.save_loading){
				return
			}
			this.save_loading = true
			setTimeout(() =>{
				
				uni.showToast({
					title:'保存成功',
					icon: 'success',
					position: 'bottom',
					success: () => {
						this.save_loading = false
						this.$store.commit('setHasTeam',true)
						// uni.hideToast();
						uni.navigateBack();
					}
				});
			},500)
		},
	}
}
